























































































































































































.publish .card-header .button {
  width: 30%;
}

.ios .publish .card-header .button {
  border-radius: 2px;
}

.android .publish .card-header {
  padding-top: 10px;
  padding-bottom: 10px;
}

.publish span.location {
  margin-right: auto;
  margin-left: 10px;
  font-size: 14px;
  color: #888;
  display: inline-block;
}

.card-content textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  box-sizing: border-box;
  border: none;
  background: none;
  border-radius: 0 0 0 0;
  box-shadow: none;
  display: block;
  padding: 8px 16px;
  margin: 0;
  width: 100%;
  height: 100px;
  color: #000;
  font-size: 17px;
  font-family: inherit;
}



