










































































































































.publish .gallery {
  width: 100%;
  margin: -4px 0;
  padding: 0 4px;
}

.publish .gallery .photo-item {
  width: calc(100%/3 - 8px);
  float: left;
  margin: 4px;
  position: relative;
}

.publish .gallery .photo-item .photo {
  padding: 50%;
  background-position: center;
  background-size: cover;
}

.publish .gallery .photo-item .photo .camera {
  background-color: #eee;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  pointer-events: none;
}

.publish .gallery .photo-item .progress-bar {
  height: 8px;
  position: absolute;
  bottom: 0px;
  width: 100%;
}

.publish .gallery .photo-item .photo input[type="file"] {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background-position: center;
  background-size: cover;
}

.publish .gallery .photo-item .exif-canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.publish .gallery .photo-item .delete-photo {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 5px;
  right: 13px;
}

.publish .list-block .item-link {
  -webkit-transition-duration: 300ms;
  transition-duration: 300ms;
  display: block;
  color: inherit;
  position: relative;
  overflow: hidden;
  z-index: 0;
}



