























html.ios .page.page-transition {
  -webkit-transition-duration: 400ms;
          transition-duration: 400ms;
}

html.ios .page.page-transition .swipeback-page-shadow {
  -webkit-transition-duration: 400ms;
          transition-duration: 400ms;
  opacity: 1
}

html.ios .page.page-enter .swipeback-page-shadow,
html.ios .page.page-leave .swipeback-page-shadow {
  opacity: 0
}

html.ios .page.page-enter,
html.ios .page.page-leave {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}

html.ios .page:first-child.page-enter,
html.ios .page:first-child.page-leave {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

html.ios .page.page-out {
  -webkit-transform: translate3d(-20%, 0, 0);
          transform: translate3d(-20%, 0, 0);
  opacity: .9;
}

.page.page-cached {
  display: none
}

html.android .page.page-transition {
  -webkit-transition-duration: 300ms;
          transition-duration: 300ms;
}

html.ios .page.page-enter .swipeback-page-shadow,
html.ios .page.page-leave .swipeback-page-shadow {
  display: none;
}

html.android .page.page-enter,
html.android .page.page-leave {
  -webkit-transform: translate3d(0, 56px, 0);
          transform: translate3d(0, 56px, 0);
  opacity: 0;
}

html.android .page:first-child.page-enter,
html.android .page:first-child.page-leave {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  opacity: 1;
}



