















































.userhome {
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.3);
}

.userhome .userhome-header {
  height: 44vw;
  background-size: cover;
  background-position: center;
  background-image: url(http://192.168.1.11/canvasImage.png);
  text-align: right;
}

.userhome .userhome-header img {
  width: 20vw;
  height: 20vw;
  border: 2px solid white;
  border-radius: 10px;
  top: 12vw;
}

.userhome .userhome-header-bg {
  position: absolute;
  top: 44vw;
  left: 16px;
  z-index: 1;
}

.userhome .userhome-header-name {
  position: absolute;
  right: 0;
  top: 48vw;
  padding: 0 8px;
  font-size: 5vw;
  letter-spacing: 1px;
  font-weight: bold;
  text-shadow: 0px 1px 2px rgba(255, 255, 255, 0.5);
}



