














html.ios .pull-to-refresh-content.getting .infinite-scroll.pull-to-refresh-layer > .preloader {
  -webkit-animation: preloader-spin 1s steps(12, end) infinite;
          animation: preloader-spin 1s steps(12, end) infinite;
}

html.android .pull-to-refresh-content.getting .infinite-scroll.pull-to-refresh-layer > .preloader {
  -webkit-animation: preloader-outer 3300ms linear infinite;
          animation: preloader-outer 3300ms linear infinite;
}

.pull-to-refresh-content:not(.getting) .infinite-scroll.pull-to-refresh-layer .preloader {
  visibility: hidden;
  -webkit-animation: none;
          animation: none;
}

.pull-to-refresh-content.getting .infinite-scroll.pull-to-refresh-layer .preloader {
  visibility: visible;
}

.pull-to-refresh-content.getting .infinite-scroll.pull-to-refresh-layer .button {
  visibility: hidden;
}



