

































































































































































.load-layer {
  position: absolute;
  left: 10%;
  top: 40%;
  z-index: 13500;
  width: 80%;
}

.load-layer .preloader {
  display: block;
  margin: 0 auto;
}

.ios .load-layer .preloader {
  width: 34px;
  height: 34px;
}

.android .load-layer .preloader {
  width: 32px;
  height: 32px;
}

.load-layer .content-block-title {
  text-align: center;
}

.load-layer .button {
  margin: 10px 25%;
}



