


























































































































































.morph.morph-transition {
  display: block;
  border-radius: 0%;
  width: auto;
  opacity: 1;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
  -webkit-transition-property: opacity, -webkit-transform, border-radius;
  -webkit-transition-property: opacity, border-radius, -webkit-transform;
  transition-property: opacity, border-radius, -webkit-transform;
  transition-property: opacity, transform, border-radius;
  transition-property: opacity, transform, border-radius, -webkit-transform;
  -webkit-transition-delay: 200ms;
  transition-delay: 200ms;
  -webkit-transition-duration: 300ms;
  transition-duration: 300ms;
}

.morph.morph-enter {
  border-radius: 100%;
  -webkit-transform: scale(0);
  transform: scale(0);
}

.morph.morph-leave {
  opacity: 0;
  border-radius: 50%;
  -webkit-transition-delay: 0ms;
  transition-delay: 0ms;
  -webkit-transition-duration: 100ms;
  transition-duration: 100ms;
}



